.app-wrapper {
	display: grid;
	max-width: 100%;
	//margin: 0 auto;
	grid-template-areas:
		"h h"
		"n c";

	grid-template-rows: 60px 1fr;
	grid-template-columns: 2fr 10fr;
}

.app-wrapper-content {
	grid-area: c;
	background-color: #323276;
	padding-left: 90px;
	padding-top: 90px;
	border-radius: 80px 52px 52px 80px;
}
